import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"

const NotFoundPage = () => (
  <Layout>
    <SEO title="404: Not found" />
    <div class="alert alert-danger" role="alert">
      <h1>غير موجــود</h1>
    </div>
    <p>تعذر فتح هذة الصفحة يمكنك العودة للصفحة الرئيسية</p>
    <Link to="/" class="btn btn-danger btn-sm">
      <i class="far fa-arrow-alt-circle-left"></i> العودة للصفحة الرئيسية
    </Link>
  </Layout>
)

export default NotFoundPage
